import { Component, OnInit } from '@angular/core';
import { Proyect } from '../../models/proyect';
import { ProyectService} from '../../services/proyect.service';
import { Global} from '../../services/global';
import { Router, ActivatedRoute, Params} from '@angular/router';


@Component({
  selector: 'app-edit-proyect',
  templateUrl: './edit-proyect.component.html',
  styleUrls: ['./edit-proyect.component.css'],
  providers:[ ProyectService]

})
export class EditProyectComponent implements OnInit {
	
	public url:string;
	public proyect: Proyect;
  public confirm: boolean;

  constructor (private _proyectService:ProyectService,
  			   private _router: Router,
  			   private _route:  ActivatedRoute) {
    this.url= Global.url; 
    this.proyect = new Proyect('','','','',2019,'','');
	  this.confirm = false;
}

  ngOnInit() {
  	this._route.params.subscribe(params =>{
  		let id= params.id;
 		this.getProyect(id);
 		console.log(id);
  		});
  }

  getProyect(id){
  	this._proyectService.getProyect(id).subscribe(
  		response => {
  				this.proyect = response.Proyect;
          console.log(this.proyect);
   			},
  		error =>{
  			console.log(<any>error);
  		}	
  		)

  }
  deleteProyect(id){
    console.log(id);
    this._proyectService.deletedProyect(id).subscribe(
      response => {
          if (response.proyect)
            {this._router.navigate(['/proyectos']);
            }
        },
       error =>{
          console.log(<any>error);
       } 
        );

  }
  setConfirm(param){
    this.confirm = param;
  }
}
