import { Component, OnInit } from '@angular/core';
import { Proyect } from '../../models/proyect';
import { ProyectService} from '../../services/proyect.service';
import { UploadService}  from '../../services/upload.service';
import { Global} from '../../services/global';
import { Router, ActivatedRoute, Params} from '@angular/router';



@Component({
  selector: 'app-edit',
  templateUrl: '../create-proyect/create-proyect.component.html',
  styleUrls: ['./edit.component.css'],
  providers:[ ProyectService, UploadService]
})
export class EditComponent implements OnInit {

  
	public title: string;
	public proyect: Proyect;
  public save_proyect: Proyect;
  
	public status : string;
	public filesToUpload: Array<File>;
	 public proyect_id:string 
  public url:string;

  constructor(private _uploadService:UploadService,  private _proyectService:ProyectService,
    private _route:  ActivatedRoute,
  	private _router: Router) {
  	this.title = 'Edicion de Proyecto';
  	this.url= Global.url; 
    console.log('constructor');
    } 

  ngOnInit() {	this._route.params.subscribe(params =>{
  		let id= params.id;
 		this.getProyect(id);
 		this.proyect_id= id;
 		console.log(id);
  		});
  }
   getProyect(id){
  	this._proyectService.getProyect(id).subscribe(
  		response => {
  				this.proyect = response.Proyect;
          console.log(this.proyect);

   			},
  		error =>{
  			console.log(<any>error);
  		}	
  		)

  }
  onSubmit(form){
    this._proyectService.updateProyect(this.proyect)
    .subscribe(
      response => {
        if (response.proyect) {
              // subir imagen
           if (this.filesToUpload){
            this._uploadService.makeFileRequest(Global.url+"upload-image/"+response.proyect._id,
                [], this.filesToUpload, "image")
            .then((result:any) =>
                  {
                        this.status ="succes";
                        this.save_proyect = response.proyect;
                       console.log(result);
                  }
                )
             }else {
              this.status ="succes";
              this.save_proyect = response.proyect;
            };
          
        } else {
          this.status = "failed";
          }
      },
      error =>{
        console.log(<any>error);
        }
    );

  }
  fileChangeEvent (fileInput:any){
    this.filesToUpload = <Array<File>>fileInput.target.files;


  }

}
