import { Component, OnInit } from '@angular/core';
import { Proyect } from '../../models/proyect';
import { ProyectService} from '../../services/proyect.service';
import { UploadService}  from '../../services/upload.service';
import { Global}  from "../../services/global";

@Component({
  selector: 'app-create-proyect',
  templateUrl: './create-proyect.component.html',
  styleUrls: ['./create-proyect.component.css'],
  providers: [ProyectService, UploadService]
})

export class CreateProyectComponent implements OnInit 
{
	public title: string;
	public proyect: Proyect;
  public status : string;
  public filesToUpload: Array<File>;
  public proyect_id:string ; 



  constructor(
  	private _proyectService: ProyectService, private _uploadService:UploadService) {
  	this.title = 'Crear Proyecto';
  	this.proyect = new Proyect('','','','',2019,'','');
    this.proyect_id = "";
  }

  ngOnInit() {
  }

  onSubmit(form){
    this._proyectService.saveProyect(this.proyect)
    .subscribe(
      response => {
        if (response.proyect) {
          

          // subir imagen
          this._uploadService.makeFileRequest(Global.url+"upload-image/"+response.proyect._id, [], this.filesToUpload, "image")
            .then((result:any) =>{
              console.log(result);
              this.status ="succes";
              this.proyect_id = response.proyect._id;
              form.reset();
              console.log(this.proyect_id);
              }
            )

        } else {
          this.status = "failed";
          }
      },
      error =>{
        console.log(<any>error);
        }
    );

  }
  fileChangeEvent (fileInput:any){
    this.filesToUpload = <Array<File>>fileInput.target.files;


  }

}
 